.hwall {
  width: 50px;
  height: 10px;
  background-color: #EEEEEE;
  position: absolute;
}

.hwall:hover {
  width: 120px;
  height: 10px;
  background-color: #DDDDDD;
  z-index: 1;
}

.vwall {
  width: 10px;
  height: 50px;
  background-color: #EEEEEE;
  position: absolute;
}

.vwall:hover {
  width: 10px;
  height: 120px;
  background-color: #DDDDDD;
  z-index: 1;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  bottom: 10px;
  /*transform-origin: top;*/
}



